	module.exports = {"data":{"markdownRemark":{"html":"<h1>The Hacker Or The Hack?</h1>\n<p>The word hacker implies the possession of certain qualities.\nQualities that kindle and fuel a knack for problem solving.\nThis ability makes hackers receptive to accomplish tasks which typically repulse others.\nAn understanding of this unique blend of qualities surfaces when answering the following questions:</p>\n<ol>\n<li>What qualities define a hacker?</li>\n<li>How are hackers related to hacks?</li>\n<li>How do hackers become hackers?</li>\n<li>What comes first, a hacker, or a hack?</li>\n</ol>\n<p>These questions are a means to diffusing a deeper understanding of hacking and hackers.\nThis article delves into answering these questions, and bringing further understanding of the words.\nThese answers shed light on the inner-hacker, and assist in framing our minds to leverage it.</p>\n<h1>Honing In</h1>\n<blockquote>\n<p>There's no sense in being precise when you don't even know what you're talking about. ~ John Von Neumann</p>\n</blockquote>\n<aside class='col-xs-12 col-sm-7 col-md-5 col-lg-4'>\n  <img style='width: 100%' src='/images/posts/hackerhack.png' />\n  <p>\n    The definitions found in the dictionary are too specific.\n    It is obvious that these words are regularly used more broadly.\n  </p>\n</aside>\n<p>The conventional definitions for <em>hack</em> and <em>hacker</em> are inadequate mainly due to two oversights:</p>\n<ol>\n<li><strong>Misrepresentation</strong>: they are stigmatized with nefarious overtones</li>\n<li><strong>Scope</strong>: their breadth is belittled to only computer security hack(ers)</li>\n</ol>\n<p>In combination, these oversights warp the story of these words.\nOnce the true meaning is uncovered one can grasp insights often overlooked by others.\nThe essence of the conversation begins with the relation the words have to each other:</p>\n<pre><code>hacker: one constructs hacks\nhack:   the culmination of a hackers effort\n</code></pre>\n<p>The relation of the words provide a vehicle for mutually defining each.\nThey also show that both hackers and hacks rely on the other.\nHowever these definitions beg the question <em>which comes first?</em>\nWe will answer that question toward the end of this article.\nBut first, each word will need to be defined.</p>\n<h2>The Hack</h2>\n<blockquote style='clear:none'>\nIt is hard to write a simple definition of something as varied as hacking, but I think what these activities have in common is playfulness, cleverness, and exploration. Thus, hacking means exploring the limits of what is possible, in a spirit of playful cleverness. Activities that display playful cleverness have \"hack value\". ~ Richard Stallman\n</blockquote>\n<p>Richard Stallman's is an acclaimed hacker, and perhaps one of the greatest of all time.\nIn his essay <a href=\"https://stallman.org/articles/on-hacking.html\">On Hacking</a>, he provides great insights on the meaning of both hackers and hacks.</p>\n<p>For Stallman, a hack is composed of 3 elements:</p>\n<ol>\n<li>playful</li>\n<li>clever</li>\n<li>explorative</li>\n</ol>\n<p>Stallman claims that something is a hack 'whether useful or not'.\nHowever, he does not mention an important caveat: <em>'a hack solves some problem'</em>.\nEven if the problem something as trivial as <a href=\"https://stallman.org/articles/on-hacking.html\">Stallman using 6 chopsticks at the same time</a>.</p>\n<p>To summarize:</p>\n<pre><code>hack: A playfully, clever and explorative solution to a problem\n</code></pre>\n<h2>The Hacker</h2>\n<blockquote>\n<p> \"The true hacker can't just sit around all night; he must pursue some hobby with dedication and flair.\" ~ Brian Harvey</p>\n</blockquote>\n<p>We can apply our insights about hacks to hackers as such:</p>\n<pre><code>Initial definitions\n-----------------------------------------------\nhack:   the culmination of a hackers effort\nhacker: one who constructs a hack\n\nDefined In Previous Section\n-----------------------------------------------\nhack: A playfully, clever and explorative solution to a problem\n\nApplying Some Algebra\n-----------------------------------------------\nhacker: one who constructs a (hack)\nhacker: one who constructs (a playfully, clever, and explorative solution to a problem)\n</code></pre>\n<p>But, there is more to a hacker than just creating hacks.\nThe following qualities must also be present:</p>\n<ol>\n<li>unquivering passion for an activity</li>\n<li>continually build knowledge and use it to solve problems</li>\n<li>approach activities like a curious child e.g. always have fun</li>\n</ol>\n<aside class='col-xs-12 col-sm-7 col-md-5 col-lg-4'>\n<h2> A Hack Of All Trades </h2>\nUnlike the dictionary, our definitions leave the subject matter to the discretion of the hacker.\nAs an example here is a sampling of some of my favorite hackers:\n<ol>\n<li>  Gina Trapini (Lifehacker) <a href='http://ginatrapani.org/'>site</a> </li>\n<li>  Richard Stallman (Software Hacker) <a href='http://en.wikipedia.org/wiki/Richard_Stallman'>wiki</a>, <a href='https://stallman.org/'>site</a> </li>\n<li>  Ramit Sethi (Budget hacker) <a href='http://www.iwillteachyoutoberich.com/blog/'>blog</a>, <a href='http://www.amazon.com/gp/product/0761147489/ref=as_li_tl?ie=UTF8&camp=1789&creative=390957&creativeASIN=0761147489&linkCode=as2&tag=richsonicom-20&linkId=T2EOTFOPGVK3KPCS'>book</a> </li>\n<li>  Benny Lewis (Language Hacker) <a href='http://www.fluentin3months.com/'>blog</a>, <a href='http://www.amazon.com/gp/product/0062282697/ref=as_li_tl?ie=UTF8&camp=1789&creative=390957&creativeASIN=0062282697&linkCode=as2&tag=richsonicom-20&linkId=OG2PZV6AM4QTDB3H'>book </a> </li>\n<li>  Kevin Mitnick (Security Hacker) <a href='http://en.wikipedia.org/wiki/Kevin_Mitnick'>wiki</a>, <a href='http://www.amazon.com/gp/product/0316037729/ref=as_li_tl?ie=UTF8&camp=1789&creative=390957&creativeASIN=0316037729&linkCode=as2&tag=richsonicom-20&linkId=AQUN4MQHRAIEFERF'>book</a> </li>\n<li>  Rolf Potts (Travel Hacker) <a href='http://www.rolfpotts.com/'>site</a>, <a href='http://www.amazon.com/gp/product/0812992180/ref=as_li_tl?ie=UTF8&camp=1789&creative=390957&creativeASIN=0812992180&linkCode=as2&tag=richsonicom-20&linkId=FGX4XXIJEP2FLWSO'>book</a> </li>\n<li>  Vanessa Van Edwards (People Skills Hacker) <a href='http://www.scienceofpeople.com/'>site</a> </li>\n<li>  Tim Feriss (Learning Hacker) <a href='http://fourhourworkweek.com/blog/'>blog</a>, <a href='http://www.amazon.com/gp/product/0547884591/ref=as_li_tl?ie=UTF8&camp=1789&creative=390957&creativeASIN=0547884591&linkCode=as2&tag=richsonicom-20&linkId=JYM3XBS67WHRRI6N'>book</a> </li>\n<li>  Babak Nivi and Naval Ravikant (Venture Hackers) <a href='http://venturehacks.com/'>blog</a> </li>\n</ol>\n</aside>\n<h1>Round Up</h1>\n<p>So far 2 of the initial questions have been answered:</p>\n<ol>\n<li><s>What qualities define a hacker?</s></li>\n<li><s>How are hackers related to hacks?</s></li>\n<li>How do hackers become hackers?</li>\n<li>What comes first, a hacker, or a hack?</li>\n</ol>\n<p>The information from the first questions can be used to answer the last.</p>\n<h1>Which Came First?</h1>\n<blockquote style='clear:none'> \"Was his action, too, a hack? I think so. Is he therefore a hacker? That depends on how much he likes to hack.\" ~ Richard Stallman</blockquote>\n<p>Why did Stallman attempt to <a href=\"https://stallman.org/articles/on-hacking.html\">use 6 chopsticks at the same time</a>?\nAccording to him: <em>'because using three in one hand is hard and ordinarily never thought of, it has \"hack value\"'</em>.\nBut, only a hacker would find 'hack value' in that.\nNon-hackers would most likely not find merit in attempting it.\nA key ingredient of hacking is an equal pleasure in both the process and the results.\nDue to this, a hacker will dig up value in activities others see no advantage.</p>\n<p>Stallman says, a hacker then is quantified by how much they like to hack (how strong that passion is).\nThat means one has to hack before becoming a hacker.\nHowever, certain hacks will only be attractive to hackers.\nA novice hacker may not find hack-value in activities experts find value in.\nThis leaves us with an interesting catch-22.</p>\n<p>Perhaps the answer is that hacker is a relative term.\nOne becomes a hacker when they meet all of the criteria, but it does not mean that they cannot hack.\nCertain hacks require a hacker, but others may have more 'hack-value' to a non-hacker.\nIn that sense, the hack comes first, but only very briefly.</p>\n<h1>Conclusion</h1>\n<ol>\n<li><s>What qualities define a hacker?</s></li>\n<li><s>How are hackers related to hacks?</s></li>\n<li><s>How do hackers become hackers?</s></li>\n<li><s>What comes first, a hacker, or a hack?</s></li>\n</ol>\n<p>These questions bring forth insights into hackers and their relationships to hacks.\nFor aspiring hackers, these can be checkpoints to strive for.\nThey expose that one should focus on more than hacks if they want to become a hacker.\nMy advise for aspiring hackers is fake it until you make it.\nKeep hacking!!!</p>\n<p>For hackers, understand that you cannot be a hacker in every domain.\nTo become a hacker you have to curate the 3 features a hacker has for that activity.</p>\n<h1>Resources</h1>\n<ol>\n<li><a href=\"https://stallman.org/articles/on-hacking.html\">On Hacking by Richard Stallman</a></li>\n<li>Gina Trapini (Lifehacker) <a href=\"http://ginatrapani.org/\">site</a></li>\n<li>Richard Stallman (Software Hacker) <a href=\"http://en.wikipedia.org/wiki/Richard_Stallman\">wiki</a>, <a href=\"https://stallman.org/\">site</a></li>\n<li>Ramit Sethi (Budget hacker) <a href=\"http://www.iwillteachyoutoberich.com/blog/\">blog</a>, <a href=\"http://www.amazon.com/gp/product/0761147489/ref=as_li_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0761147489&#x26;linkCode=as2&#x26;tag=richsonicom-20&#x26;linkId=T2EOTFOPGVK3KPCS\">book</a></li>\n<li>Benny Lewis (Language Hacker) <a href=\"http://www.fluentin3months.com/\">blog</a>, <a href=\"http://www.amazon.com/gp/product/0062282697/ref=as_li_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0062282697&#x26;linkCode=as2&#x26;tag=richsonicom-20&#x26;linkId=OG2PZV6AM4QTDB3H\">book </a></li>\n<li>Kevin Mitnick (Security Hacker) <a href=\"http://en.wikipedia.org/wiki/Kevin_Mitnick\">wiki</a>, <a href=\"http://www.amazon.com/gp/product/0316037729/ref=as_li_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0316037729&#x26;linkCode=as2&#x26;tag=richsonicom-20&#x26;linkId=AQUN4MQHRAIEFERF\">book</a></li>\n<li>Rolf Potts (Travel Hacker) <a href=\"http://www.rolfpotts.com/\">site</a>, <a href=\"http://www.amazon.com/gp/product/0812992180/ref=as_li_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0812992180&#x26;linkCode=as2&#x26;tag=richsonicom-20&#x26;linkId=FGX4XXIJEP2FLWSO\">book</a></li>\n<li>Vanessa Van Edwards (People Skills Hacker) <a href=\"http://www.scienceofpeople.com/\">site</a></li>\n<li>Tim Feriss (Learning Hacker) <a href=\"http://fourhourworkweek.com/blog/\">blog</a>, <a href=\"http://www.amazon.com/gp/product/0547884591/ref=as_li_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0547884591&#x26;linkCode=as2&#x26;tag=richsonicom-20&#x26;linkId=JYM3XBS67WHRRI6N\">book</a></li>\n<li>Babak Nivi and Naval Ravikant (Venture Hackers) <a href=\"http://venturehacks.com/\">blog</a></li>\n</ol>","frontmatter":{"title":"What Came First The Hacker Or The Hack","hero":"/images/posts/hacker.jpg"},"fields":{"date":"2014-11-02"}}},"pathContext":{"url":"/posts/2014-11-02-what-came-first-the-hacker-or-the-hack/"}}

